.wrapper {
    width: 100%;
    display: flex;
    justify-content: center;
    padding-top: 3em;
    padding-inline: 3em;

    article {
        max-width: 110ch;
        border-radius: 20px;
        box-shadow: 1px 1px 10px rgba(1, 1, 1, 0.2);
        margin-bottom: 5em;
        background-color: white;

        .image {
            width: 100%;

            img {
                width: 100%;
                max-height: 450px;
                object-fit: cover;
                border-radius: 20px;
                border-bottom-right-radius: 0;
                border-bottom-left-radius: 0;
            }
        }

        .blogContent {
            width: 100%;
            padding: 2em;

            width: 100%;

            .date {
                margin-bottom: 0.5em;
            }

            .title {
                font-size: 1.3rem;
                margin-bottom: 1em;
            }

            .body {
                color: #8a8a8a;
                line-height: 30px;

                img {
                    max-width: 100%;
                }
            }

            footer {
                display: flex;
                align-items: center;
                margin-top: 2rem;

                .userImg {
                    img {
                        width: 50px;
                        height: 50px;
                        border-radius: 50%;
                    }
                }

                .userInfo {
                    padding-left: 0.8em;
                    .occupation {
                        color: #8a8a8a;
                        font-size: 0.9em;
                    }
                }
            }
        }
    }
}

@media screen and (max-width: 600px) {
    .wrapper {
        padding-inline: 0;
        padding-top: 0;

        article {
            width: 100%;
            border-radius: 0;

            .image {
                img {
                    border-radius: 0;
                }
            }
        }
    }
}

@media screen and (max-width: 450px) {
    .wrapper {
        article {
            .blogContent {
                padding-inline: 1em;
            }
        }
    }
}
